.panel-overlay {
  background: linear-gradient(217deg, grey, white, grey);
  background-color: white;
  box-shadow: inset 5px 5px 5px whitesmoke, inset -4px -4px 5px black, 4px 4px 14px rgba(0,0,0,.1);
  padding: 40px;
  margin: 20px auto;
  border-radius: 12px;
  color: #0D1B2A;
  width: 80%;
  max-height: 350px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  font-family: 'Segoe UI', sans-serif;
  font-size: small;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-button {
  width: 75px;
  height: 50px;
  font-size: 9px;
  margin: 10px 0;
  padding: 0%;
}

.table-container {
  width: 100%;
  overflow-y: auto; /* Habilitar scroll vertical dentro de la tabla */
  margin-bottom: 20px;
}

.modal-content {
  display: flex;
  max-width: 700px;
  flex-direction: column;
  align-items: center;
  color:black;
  font-family: 'Segoe UI', sans-serif;
  font-size: small;
}

.recepciones-table {
  border-collapse: collapse;
  width: 100%;
  overflow-y: auto;
}

.recepciones-table th,
.recepciones-table td {
  border: 1px solid #cccccc;
  padding: 10px;
  text-align: center;
  align-items: center;
}

.recepciones-table th {
  background-color: #96c8ec;
  font-weight: bold;
}

.recepciones-table tr:hover {
  background-color: #f1f1f1;
}

.highlight-orange {
  background-color: orange;
  color: white; /* Opcional: para mejorar la legibilidad */
  font-weight: bold; /* Opcional: para resaltar el texto */
}